		startP5: function() {
			this.radar.startLoopP5();
		},
		stopP5: function() {
			this.radar.stopLoopP5();
		},
		startEffect: function()
		{
			console.log("testTito")
			if (this.isPlayP5) {
				/*this.isPlayP5 = false;
				this.ps = null;
				delete this.ps;*/
				this.stopP5();
